import React from 'react';
import ReactWordcloud from 'react-wordcloud';

import tech from './techConfig.json';

const Languages = () => (
  <div style={{ height: '100%', width: '100%' }}>
    <ReactWordcloud words={tech} options={{
      fontFamily: 'Lato',
      rotations: 0,
      fontSizes: [14, 42],      
      enableTooltip: false,
    }} />
  </div>
)

export default Languages;
