import differenceInYears from 'date-fns/differenceInYears';

export const textToAnchor = (text) => text.toLowerCase().replaceAll(' ', '-');

export const dateDiffYears = (from, to) => differenceInYears(new Date(from), new Date(to));

export const getPublicURL = (path) => `${process.env.PUBLIC_URL}/${path}`;
