import React from 'react';
import ReactWordcloud from 'react-wordcloud';

import personalAttConfig from './personalAttConfig.json';

const Languages = () => (
  <div style={{ height: '100%', width: '100%' }}>
    <ReactWordcloud words={personalAttConfig} options={{
      fontFamily: 'Lato',
      rotations: 0,
      fontSizes: [14, 42],
      padding: 20,
      enableTooltip: false,
    }}
    />
  </div>
)

export default Languages;
