import React from 'react';
import PropTypes from 'prop-types';

import STYLES from './IconText.scss';
import { getPublicURL } from "../Utils/common";

const c = className => STYLES[className] || 'UNKNOWN';

const IconText = ({ src, altText, children, link, className }) => {

  const divClassNames = [c('imageBox')];
  const linkClassNames = [c('link')];

  if (link) {
    linkClassNames.push(className);
  } else {
    divClassNames.push(className);
  }

  const body = (
    <div className={divClassNames.join(' ')}>
      <img src={getPublicURL(src)} alt={altText} width={32}/>
      {children}
    </div>
  );

  return link
    ? (<a className={linkClassNames.join(' ')} href={link}>
      {body}
    </a>)
    : body;
}

IconText.propTypes = {
  src: PropTypes.string.isRequired,
  altText: PropTypes.string.isRequired,
  children: PropTypes.arrayOf(PropTypes.node),
  link: PropTypes.string,
};

IconText.defaultProps = {
  link: '',
  children: [],
};

export default IconText;
