import React, {useState} from 'react';

import ChevronDownIcon from 'bpk-component-icon/sm/chevron-down'
import { withButtonAlignment } from 'bpk-component-icon';
import BpkButton from 'bpk-component-button';

import jobs from './jobs.json';

import STYLES from './Experience.scss';
import { getPublicURL } from "../Utils/common";

const c = className => STYLES[className] || 'UNKNOWN';

const AlignedDownIcon = withButtonAlignment(ChevronDownIcon);

const JobInfo = ({ job, expanded }) =>
  <details className={c('jobDetails')} key={job.company} open={expanded}>
    <summary>
      <h3>
        {
          !!job.logo
            ? <img src={getPublicURL(job.logo)} className={c('jobLogo')}/>
            : <span>{job.company}</span>
        }
        ({job.start}{!!job.end ? ` - ${job.end}` : ''})
      </h3>
    </summary>
    <div className={c('roles')}>
      {job.roles.map(role => (
        <React.Fragment key={role.name}>
          <h4>
            {role.name}:
          </h4>
          <span>{role.description}</span>
        </React.Fragment>
      ))}
    </div>
  </details>

const Experience = () => {
  const [ showMore, setShowMore ] = useState(false);

  const chevronClassNames = [c('chevron')];
  const moreJobsClassNames = [c('moreJobsColumn'), c('notVisible')];
  if (showMore) {
    chevronClassNames.push(c('chevronUp'));
    moreJobsClassNames.push(c('show'));
  }

  return (
    <div>
      <div>
        {jobs.filter(j => !j.collapsed).map((job, index) => {
          console.log(job, index);
          return <JobInfo job={job} expanded={index===0}/>
        })}
      </div>
      <div>
        <BpkButton iconOnly onClick={() => setShowMore(!showMore)} className={chevronClassNames.join(' ')}>
          <AlignedDownIcon />
        </BpkButton>
      </div>
      <div className={moreJobsClassNames.join(' ')}>
        {jobs.filter(j => j.collapsed).map((job) => <JobInfo job={job} expanded={false}/>)}
      </div>
    </div>
  );
}

export default Experience;
