import React from 'react';
import ProgressBar from 'react-animated-progress-bar';

import languages from './languagesConfig.json';

import STYLES from './Languages.scss';

const c = className => STYLES[className] || 'UNKNOWN';

const langLevelMap = {
  "A1": "16",
  "A2": "27",
  "B1": "49",
  "B1.2": "55",
  "B2": "64",
  "B2.2": "72",
  "C1": "82",
  "C2": "95",
}

const Languages = () => (
  <div className={c('languagesWrapper')}>
    {languages.map(lang => (
      <div className={c('languageContainer')} key={lang.lang}>
        <ProgressBar width="230" trackWidth="13" percentage={langLevelMap[lang.level]} />
        {lang.lang}
      </div>
    ))}
  </div>
)

export default Languages;
